import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Counter from 'components/Counter';
import Face from 'components/Face';
import MinesweeperContext from 'contexts/minesweeper';
import useCallbackRef from 'hooks/use-callback-ref';
import useDidUpdate from 'hooks/use-did-update';
import useInterval from 'hooks/use-interval';
import React, { useContext, useState } from 'react';

import styles from './styles.module.scss';

const getFaceIcon = ({ isSolved, isBusted }: MineFieldStatus) => {
  if (isSolved) {
    return <FontAwesomeIcon icon={['far', 'smile']} />;
  }

  if (isBusted) {
    return <FontAwesomeIcon icon={['far', 'frown']} />;
  }

  return void 0;
};

const Indicators = () => {
  const { mineFieldStatus, mineFieldParams, reset } = useContext(
    MinesweeperContext
  );

  const { isSetup, isSolved, isBusted, hiddenMinesCount } = mineFieldStatus;

  const [secondsCount, setSecondsCount] = useState(0);

  const handleFaceClick = useCallbackRef(() => {
    setSecondsCount(0);
    reset();
  });

  useInterval(
    () => {
      setSecondsCount(secondsCount + 1);
    },
    isSetup && !(isSolved || isBusted) ? 1e3 : void 0
  );

  useDidUpdate(() => {
    setSecondsCount(0);
  }, mineFieldParams);

  return (
    <div className={styles.indicators}>
      <Counter count={hiddenMinesCount} />
      <Face icon={getFaceIcon(mineFieldStatus)} onClick={handleFaceClick} />
      <Counter count={secondsCount} />
    </div>
  );
};

export default Indicators;
