import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import classNames from 'classnames';
import React, { memo, SyntheticEvent } from 'react';
import CellChecker from 'utils/cell-checker';

import Bomb from '../Bomb';
import styles from './styles.module.scss';

interface Props {
  cell: Cell;
  onClick?: (cell: Cell) => void;
  onMouseDown?: (event: SyntheticEvent<HTMLButtonElement>, cell: Cell) => void;
  onContextMenu?: (cell: Cell) => void;
}

const Cell = ({ cell, onClick, onMouseDown, onContextMenu }: Props) => {
  const {
    isHidden,
    isEmpty,
    isFlagged,
    isMined,
    isWrong,
    isBusted,
  } = new CellChecker(cell);

  const { value } = cell;

  const visibleStyles = classNames(styles.cell, styles.visible);

  const handleClick = () => {
    onClick?.(cell);
  };

  const handleContextMenu = () => {
    onContextMenu?.(cell);
  };

  const handleMouseDown = (event: SyntheticEvent<HTMLButtonElement>) => {
    onMouseDown?.(event, cell);
  };

  if (isHidden) {
    return (
      <button
        type="button"
        aria-label="cell"
        className={styles.cell}
        onClick={handleClick}
        onContextMenu={handleContextMenu}
      />
    );
  }

  if (isFlagged) {
    return (
      <button
        type="button"
        className={styles.cell}
        onContextMenu={handleContextMenu}
      >
        <FontAwesomeIcon icon={['far', 'flag']} />
      </button>
    );
  }

  if (isEmpty) {
    return <button type="button" aria-label="cell" className={visibleStyles} />;
  }

  if (isBusted) {
    return <Bomb className={classNames(styles.cell, styles.busted)} />;
  }

  if (isMined) {
    return <Bomb className={visibleStyles} />;
  }

  if (isWrong) {
    return <Bomb className={classNames(visibleStyles, styles.wrong)} />;
  }

  return (
    <button
      type="button"
      className={classNames(visibleStyles, styles[`value${value}`])}
      onMouseDown={handleMouseDown}
    >
      {value}
    </button>
  );
};

export default memo(Cell);
