import React, { memo } from 'react';

const formatCount = (count: number) => {
  if (count < 1) {
    return '000';
  }

  if (count < 10) {
    return `00${count}`;
  }

  if (count < 100) {
    return `0${count}`;
  }

  return count.toString();
};

const Counter = ({ count }: { count: number }) => <>{formatCount(count)}</>;

export default memo(Counter);
